<template>
  <div class="ruleGroup-modal">
    <a-modal
      :title="title"
      :visible="ruleGroupVisible"
      @cancel="handleCancel"
      cancelText="取消"
      okText="确定"
      :mask="false"
      :maskClosable="false"
      :width="600"
      class="modalCls"
    >
      <a-form-model ref="ruleGroupForm" :model="ruleGroupForm" :rules="ruleGroupFormRule">
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="规则组名称" prop="name">
              <a-input v-model="ruleGroupForm.name" placeholder="请输入规则组名称" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="规则组唯一标识" prop="code">
              <a-input v-model="ruleGroupForm.code" placeholder="请输入规则组唯一标识" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="文件名正则" prop="fileRegex">
              <a-input v-model="ruleGroupForm.fileRegex" placeholder="请输入识别文件名的正则" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="文件编码" prop="fileEncode">
              <a-select v-model="ruleGroupForm.fileEncode"  placeholder="请输入文件编码类型">
                <a-select-option value="utf-8">utf-8</a-select-option>
                <a-select-option value="gbk">gbk</a-select-option>
                <a-select-option value="utf-16">utf-16</a-select-option>
                <a-select-option value="unicode">unicode</a-select-option>
                <a-select-option value="latin1">latin1</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="分析对象" prop="objectId">
              <a-select v-model="ruleGroupForm.objectId" placeholder="请选择分析对象" allowClear>
                  <a-select-option :value="obj.id" v-for="obj of objectData" :key="obj.id">{{obj.objectName}}</a-select-option>
                </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="规则组描述" prop="description">
              <a-textarea v-model="ruleGroupForm.description" placeholder="请输入规则组描述" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
          <a-button type="link" ghost @click="handleCancel">取消</a-button>
          <a-button type="primary" @click="handleOk">提交</a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "ruleGroup-modal",
  props: {
    // 模态框的显示与隐藏
    ruleGroupVisible: {
      type: Boolean,
      default: false,
      required: true,
    },
    // 模态框的标题
    title: {
      type: String,
      default: "添加/编辑规则组",
    },
    // 表单内容 一般添加的时候才会传入
    content: {
      type: Object,
      default: function () {
        return {
          name: "",
          code:"",
          objectId:"",
          description: "",
          creator: "",
          gmtCreate: "",
          gmtModified: "",
        };
      },
    },
    // 关于操作类型 0为添加 1为编辑
    optType:{
      type:Number,
      default:0,
      required: true,
    }
  },
  data() {
    return {
      ruleGroupFormRule: {
        name: [{ required: true, message: "请输入规则组名称",trigger:'blur' }],
        code: [{ required: true, message: "请输入规则组唯一标识",trigger:'blur' }],
        fileRegex: [{ required: true, message: "请输入识别文件名的正则",trigger:'blur' }],
        fileEncode: [{ required: true, message: "请输入文件编码类型",trigger:'blur' }],
        objectId: [{ required: true, message: "请选择分析对象",trigger:'change' }],
      },
      ruleGroupForm:{
          name: "",
          code:"",
          fileRegex:"",
          fileEncode:"",
          objectId:"",
          description: "",
      }
    };
  },
  computed: {
    ...mapState(["objectData"]),
  },
  methods: {
    handleOk() {
        this.$refs.ruleGroupForm.validate(valid => {
            if(valid){
                this.$emit("ruleGroupOk", {formContent:this.ruleGroupForm,optType:this.optType});
            } else {
                this.$message.warning('输入有误，请检查');
                return false
            }
        })
    },
    handleCancel() {
      this.$emit("ruleGroupCancel", "我点击取消");
    },
  },
  watch:{
    content:{
      handler:function(newVal){
        Object.assign(this.ruleGroupForm,newVal);
      },
      deep:true
    },
    ruleGroupVisible:{
      handler:function(newVal){
        if(newVal){
          this.$store.dispatch("getObjectData", this);
        }
      }
    }
  },
};
</script>
