<template>
  <div class="ruleGroup">
    <div class="tableTopTools clearfix">
      <p class="toolName fl">工具栏：</p>
      <div class="buttons fl">
        <a-button type="primary" @click="optRuleGroup">
          <a-icon type="plus"></a-icon>添加规则组
        </a-button>
      </div>
    </div>
    <div class="tableContent">
      <a-table
        :columns="columns"
        :dataSource="ruleGroupData"
        rowKey="id"
        :pagination="false"
        @change="onSortChange"
      >
        <span slot="objectId" slot-scope="text,record">
          <span>{{record.objectId | getObjectCnById(that)}}</span>
        </span>
        <span slot="gmtCreate" slot-scope="text,record">
          <span>{{record.gmtCreate | dateFormatter(1)}}</span>
        </span>
        <span slot="gmtModified" slot-scope="text,record">
          <span>{{record.gmtModified | dateFormatter(1)}}</span>
        </span>
        <span slot="action" slot-scope="text,record">
          <a class="t-optColor-normal" @click="editRuleGroup(record)">编辑</a>
          <a class="t-optColor-warning" @click="deleteRuleGroup(record)">删除</a>
        </span>
      </a-table>
      <div class="ar" style="margin-top:30px" v-if="ruleGroupData.length > 0">
        <a-pagination
          v-model="pageParam.page"
          :total="total"
          :page-size="pageParam.size"
          @change="pageChange"
        ></a-pagination>
      </div>
    </div>
    <ruleGroupModal
      :ruleGroupVisible="ruleGroupVisible"
      @ruleGroupOk="ruleGroupOk"
      @ruleGroupCancel="ruleGroupCancel"
      :content="content"
      :optType="optType"
    ></ruleGroupModal>
  </div>
</template>
<script>
import ruleGroupModal from "@/components/myModal/ruleGroup-modal.vue";
export default {
  name: "ruleGroup",
  components: { ruleGroupModal },
  data() {
    return {
      // 表格列
      columns: [
        {
          dataIndex: "name",
          title: "规则组名称",
          sorter:true
        },
        {
          dataIndex: "code",
          title: "规则组唯一标识",
        },
        {
          dataIndex: "fileRegex",
          title: "文件名正则",
        },
        {
          dataIndex: "fileEncode",
          title: "文件编码",
        },
        {
          dataIndex: "description",
          title: "规则组描述",
          ellipsis:true
        },
        {
          dataIndex: "objectId",
          title: "所属分析对象",
          scopedSlots: { customRender: "objectId" },
        },
        {
          dataIndex: "creator",
          title: "创建者",
        },
        {
          dataIndex: "gmtCreate",
          title: "创建时间",
          scopedSlots: { customRender: "gmtCreate" },
          sorter:true
        },
        {
          dataIndex: "gmtModified",
          title: "最近更新时间",
          scopedSlots: { customRender: "gmtModified" },
        },
        {
          dataIndex: "action",
          title: "操作",
          scopedSlots: { customRender: "action" },
        },
      ],
      // 表格数据
      ruleGroupData: [],
      // 操作模态框的显示 隐藏
      ruleGroupVisible: false,
      // 操作模态框的内容
      content: {
        name: "",
        code:"",
        objectId: "",
        description: "",
        creator: "",
        gmtCreate: "",
        gmtModified: "",
        order: "desc",
        field:""
      },
      // 模态框的操作类型 0添加 1编辑
      optType: 0,
      pageParam: {
        page: 1,
        size: 10,
      },
      total: 0,
      that: this,
    };
  },
  created() {
    this.getRuleGroup();
  },
  methods: {
    //  查询规则组
    getRuleGroup() {
        if(this.field){
            this.pageParam.field = this.field
        }
        if(this.order){
            this.pageParam.order = this.order
        }
      this.$API.searchRuleGroup(this.pageParam).then((res) => {
        if (res.code === 1000) {
          this.ruleGroupData = res.data;
          this.total = res.total;
          if (this.pageParam.page > 1 && this.ruleGroupData.length === 0) {
            this.pageParam.size--;
            this.getRuleGroup();
          }
        } else {
          this.ruleGroupData = [];
        }
      });
    },
    optRuleGroup() {
      this.resetContent();
      this.ruleGroupVisible = true;
      this.optType = 0;
    },
    //   编辑
    editRuleGroup(column) {
      this.content = column;
      this.optType = 1;
      this.ruleGroupVisible = true;
    },
    // 删除
    deleteRuleGroup(column) {
      const that = this;
      this.$confirm({
        title: "确定删除该条数据吗？",
        content: () => <div style="color:red">删除后将不可恢复</div>,
        okText: "确认删除",
        cancelText: "取消删除",
        onOk() {
          that.$API.delRuleGroup(column.id).then((res) => {
            if (res.code === 1000) {
              that.$message.success("删除成功");
              that.getRuleGroup();
            } else {
              that.$message.warning(res.message);
            }
          });
        },
      });
    },
    // 添加unit
    ruleGroupOk(val) {
      if (val.optType) {
        let param = Object.assign({}, val.formContent, { id: this.content.id });
        this.$API.editRuleGroup(param).then((res) => {
          if (res.code === 1000) {
            this.$message.success("规则组更新成功");
            this.ruleGroupVisible = false;
            this.getRuleGroup();
          } else {
            this.$message.warning(res.message);
          }
        });
      } else {
        this.$API.addRuleGroup(val.formContent).then((res) => {
          if (res.code === 1000) {
            this.$message.success("添加规则组成功");
            this.ruleGroupVisible = false;
            this.getRuleGroup();
          } else {
            this.$message.warning(res.message);
          }
        });
      }
    },
    // 取消unit
    ruleGroupCancel() {
      this.ruleGroupVisible = false;
      this.resetContent();
    },
    pageChange() {
      this.getRuleGroup();
    },
    // 搜索关键词变化事件
    keywordChange() {
      this.pageParam.page = 1;
      this.getRuleGroup();
    },
    // 清洗content
    resetContent() {
      this.content = {
        name: "",
        code: "",
        objectId: "",
        description: "",
        creator: "",
        gmtCreate: "",
        gmtModified: "",
      };
    },
      // 排序
      onSortChange(pagination,filter,soter){
          if(soter.order==="ascend"){
              soter.order="asc";
          }else if(soter.order==="descend"){
              soter.order="desc";
          }
          if(soter.field==="gmtCreate"){
              soter.field = "gmt_create"
          }
          this.page = 1;
          this.order = soter.order;
          this.field = soter.field;
          this.getRuleGroup();
      },
  },
};
</script>